import * as React from 'react';

export interface ISliderProps {
  label: string;
  minValue: number;
  maxValue: number;
  value: number;
  allowDecimals?: boolean;
}

export interface ISliderState {
  value: number;
}

export default class Slider extends React.Component<ISliderProps, ISliderState> {

  public static initialProps: {} = {
    label: '',
    minValue: 0,
    maxValue: 10,
    allowDecimals: false
  };

  public static initialState: {} = {
    value: 0
  };

  public render(): React.ReactElement<ISliderProps> {
    return (
      <div className='ms-Slider'>
        <span className='ms-Slider-description'>{ this.props.label }</span>
        <input type='range' className='ms-Slider-input' />
        <label className='ms-Toggle-field'>
          <span className='ms-Label ms-Label--off'>{ this.props.value }</span>
        </label>
      </div>
    );
  }
}
